import React from 'react';
import './index.css';

const getAge = () => {
  const date = new Date();
  const birthDate = new Date('1997-03-06');
  return birthDate.getMonth() > date.getMonth()
    ? date.getFullYear() - birthDate.getFullYear() - 1
    : date.getFullYear() - birthDate.getFullYear();
}

export default () => {
  return (
    <div className="about">
      <div className="blurb">
        <p>
          I am a {getAge()} year old graduate with a Bachelor of Science. I believe in working hard and intelligently. My approach to problem solving can be characterized in three ways: psychological, philosophical and programmatic. Hence, I have a degree in Psychology, minoring in Philosophy, and know full-stack development.
      </p>
      </div>
    </div>

  )
}


