/* harmony default export */ __webpack_exports__["a"] = ([{alt:"Electron",path:"/svg/electron.svg",blurb:'I learned electron to build a cross-platform desktop application using JavaScript. This fortified my knowledge of Node and my back-end development skills. Through this process I also began to appreciate the utility of well-documented API.',color:'blue'},{alt:"React",path:"/svg/react.svg",blurb:'After creating non-reactive dynamic websites from scratch, I realized the importance of reactivity. Reactivity would allow me to modularize the web app into components, speed up navigation and load times, and provide extensibility. Thus, I learned React, redux, and react-router by creating a server-side rendered React app with authentication and database support. In particular, this process sold me on functional programming paradigms and fully exploiting ES6 features.',color:'aqua'},{alt:"JavaScript",path:"/svg/js.svg",blurb:'JavaScript lies at the heart of my coding skillset and is now something I fight with, instead of against. I learnt JavaScript after Python and was initially aversed to its syntax, but as my understanding of the language grew, so too did my appreciation for its nuances and ability to utilize them.',color:'yellow'},{alt:"Node JS",path:"/svg/nodejs.svg",blurb:'Node provided the back-end yin to the front-end yang of my development stack. Through it, I could fully indulge my interest in mathematics and logical thinking. In conjunction with Electron, Node highlighted the importance of software architecture, and to organize back-end API in terms of function rather than implementation. I use MongoDB for any databases.',color:'chartreuse'},{alt:"Vue",path:"/svg/vue.svg",blurb:'I have used Vue less than React, but still appreciate their differences and (dis)advantages. I found Vue very easy to pick up, and it introduced me to reactivity in a simpler way than React did.',color:'aquamarine'},{alt:"HTML and CSS",path:"/svg/htmlcss.svg",blurb:"At the base of any front-end developer's stack are HTML and CSS. In addition to CSS, I know SCSS and am well-versed in all three. To augment these, I am also versed in Adobe Illustrator, Adobe Photoshop and SVG with CSS to achieve the aesthetic and UX I strive for.",color:'darkorange'}]);

