import React from 'react';
import projects from '../../model/projects';
import Project from '../../components/Project';
import './index.css';

export default () => {
  return (
    <div className="projects page" id="projects">
      <div className="header">
        <h1>Projects</h1>
        <p>Below are some of the projects I have created or been involved in.</p>
      </div>
      <div className="cards">
        {projects.map((project, i) =>
          <Project
            {...project}
            key={i}
          />)}
      </div>
      <p>I am happy to elaborate on these upon request.</p>
    </div>
  )
}


