import React, { Component } from 'react';
import './index.css';
import Rodal from 'rodal';
import 'rodal/lib/rodal.css';

export default class Project extends Component {
  state = {
    expanded: false,
    intervalID: null,
    index: 0
  }
  show = (pics) => () => {
    if(window.innerWidth > 750){
      if (pics) this.setState({ visible: true });
    }
  }
  componentDidMount = () => {
    const intervalID = setInterval(this.slide, 3000);
    this.setState({ intervalID });
  }
  componentWillUnmount = () => {
    clearInterval(this.state.intervalId);
  }
  slide = () => {
    const { pics } = this.props;
    if (pics) {
      if (pics.length - 1 > this.state.index) {
        this.setState({ index: this.state.index + 1 });
      }
      else {
        this.setState({ index: 0 })
      }
    }
  }
  hide = (pics) => () => {
    if (pics) this.setState({ visible: false });
  }
  render = () => {
    const { name, github, src, blurb, style, pics, desc, tech } = this.props;
    const background = {
      background: `url(${src}) no-repeat`,
      backgroundSize: 'cover'
    }
    return (
      <React.Fragment>
        <div className={`Project ${pics ? 'clickable' : ''}`} style={style} onClick={this.show(pics)}>
          <div className="image" style={background}>
          </div>
          <div className="details">
            <h1>{name}</h1>
            {blurb}
            <div className="tech">
              {tech && tech.map(({logo, name}, i) => 
              <div className="logo">
                <img src={logo} alt={name}/>
                <p className="tech-name">{name}</p>
              </div>
              )}
            </div>
          </div>
          <div className="actions">
            <a href={github} target="_blank" rel="noopener noreferrer">{github && 'Github'}</a>
          </div>
        </div >
        <Rodal
          visible={this.state.visible}
          onClose={this.hide(pics)}
          width={80}
          height={80}
          measure='%'
          customStyles={{ borderRadius: '20px', padding: '40px' }}
        >
          <h2>{name}</h2>
          <div className="gallery">
            <div className="pics cell">
              {pics && <img
                src={pics[ this.state.index ]}
                className='pic'
              />}
            </div>
            <div className="desc cell">
              {desc}
            </div>
          </div>
        </Rodal>
      </React.Fragment>
    )
  }
}


