import React from 'react'
import './index.css'

export default ({ name, src, location }) => {
  const handleClick = location => () => {
    window.location = location
  }
  return (
    <div
      className='Landing-icon'
      onClick={handleClick(location)}
    >
      <img src={src} alt={name} />
      <h2>
        {name}
      </h2>
    </div>
  )
}



