import React from 'react';
import { education } from '../../model/headers';
import Header from '../../components/Header';
import Blurb from '../../components/Blurb';

export default () => {
  return (
    <div className="education page" id="education">
      <Header {...education} />
      <Blurb blurb={education.blurb} />
    </div>
  )
}


