import React from 'react';
import './index.css';

export default ({ path, alt, active, handler, index }) => {
  return (
    <div className={`icon ${active === index ? 'active' : ''}`} >
      <img src={path} alt={alt} onClick={handler} />
      <h2>{alt}</h2>
    </div>
  )
}


