import React from 'react';
import './index.css';

export default ({ title, blurb, index, active, color, handler }) => {
  const spread = 4;
  const style = {
    left: `${(index * spread)}%`,
    zIndex: 10 - index,
    background: color
  }
  return (
    <div className={`Card ${active === index ? 'active' : ''} Card-${index}`} style={style} onClick={handler}>
      <h1>{title}</h1>
      <p>
        {blurb}
      </p>
    </div>
  )
}


