import React from 'react';
import { selfTaught } from '../../model/headers';
import Header from '../../components/Header';
import Blurb from '../../components/Blurb';
import './index.css'

export default () => {
  return (
    <div className="self-taught page" id="self-taught">
      <Header {...selfTaught} />
      <Blurb blurb={selfTaught.blurb}/>
    </div>
  )
}


